import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalController, AlertController, IonInfiniteScroll } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { InventariosService } from 'src/app/providers/Api/Inventarios.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { IvtModalPage } from '../ivt-modal/ivt-modal.page';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ivtproductos',
  templateUrl: './ivtproductos.page.html',
  styleUrls: ['./ivtproductos.page.scss'],
})
export class IVTproductosPage implements OnInit {
  productos = [];
  load = false;

  patron = '';
  desde = 0;

  seleccion = 'Todos';
  mensaje = 'No hay productos registrados';
  prod = true;
  @ViewChild('inifinito', {static: false}) infinite: IonInfiniteScroll;
  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private alertCtrl: AlertController,
    private ActiveRouter: ActivatedRoute
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    if (this.ActiveRouter.snapshot.paramMap.get('modal') === 'agotados') {
      this.seleccion = 'Agotados';
      this.cargarProductosAgotados();
    } else if (this.ActiveRouter.snapshot.paramMap.get('modal') === 'proximos') {
      this.seleccion = 'Próximos en agotarse';
      this.cargarProductosProximos();
    } else {
      this.cargarProductos();
    }
    
  }

  refrescando( event ) {
    this.cargarProductos().then(() => {
      event.target.complete();
    }).catch(() => event.target.complete());
  }


  async cargarProductos() {
    this.desde = 0;
    if (this.load) {
      
      this.infinite.disabled = false;
    }
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.ivtApi.leerProducto(info.token, {desde: this.desde, id: this.ActiveRouter.snapshot.paramMap.get('id') } ).subscribe((data: any) => {
        if (data.exe) {
            this.productos = data.response;
        } else {
          this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        }
        this.load = true;
      }, (err) => {
          this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
      })
    }
}

  async modal() {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'productos',
        inventario: this.ActiveRouter.snapshot.paramMap.get('id')
      },
    });

    await modal.present();

    const cerrado = await modal.onWillDismiss();

    if (cerrado.data) {
      if (cerrado.data.exe) {
        this.cargarProductos();
      }
    }
  }



  async editar( producto ) {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'editarproductos',
        producto
      },
    });

    await modal.present();

    const cerrado = await modal.onWillDismiss();

    if (cerrado.data) {
      if (cerrado.data.exe) {
        this.cargarProductos();
      }
    }
  }

  async eliminar ( producto: any, i ) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
       const alert = await this.alertCtrl.create({
         header: `Eliminar Producto: ${producto.nombre}`,
         buttons: [
           {
             role: 'cancel',
             text: 'Cancelar'
           },
           {
             text: 'Eliminar',
             cssClass: 'rojoi',
             handler: () => {
                // tslint:disable-next-line:max-line-length
                this.ivtApi.eliminarProducto(info.token, producto._id).subscribe((res: any) => {
                    
                  if ( res.exe ) {
                    this.toast.toastExitoso('Producto eliminado!');
                    this.productos.splice(i, 1);
                  } else {
                    this.toast.toastError('No se pudo eliminar el producto, intentelo más tarde');
                  }
                }, (err) => {
                  this.toast.toastError('No se pudo eliminar el proveedor, intentelo más tarde');
                })
             }
           }
         ]
       })

       await alert.present();
    }
  }

  async buscar(event) {
    console.log(event.detail, 'epale');
    
    if ( event.detail.value !== '') {
      this.desde = 0;
      this.infinite.disabled = false;
      this.patron = event.detail.value;

      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.leerProducto(info.token, {desde: this.desde, id: this.ActiveRouter.snapshot.paramMap.get('id'), patron: this.patron }).subscribe((data: any) => {
          console.log(data, 'mi data');
          if ( data.exe ){
            this.productos = data.response;
            if ( data.response.length === 0) {
              this.desde = 0;
            }
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
    } else {
      this.desde = 0;
      
      console.log(this.infinite.disabled)
      this.cargarProductos();
    }
  }

  async loadData( event ) {
    // event.target.complete()
    this.desde = this.desde + 5;

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      if ( this.patron === '') {
        this.ivtApi.leerProducto(info.token, {desde: this.desde, id: this.ActiveRouter.snapshot.paramMap.get('id') }).subscribe((data: any) => {
          console.log(data);
          if (data.exe) {
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.productos.push(hoja);
              })
              event.target.complete();
             } else {
              event.target.disabled = true;
              this.desde = 0;
             }
          } else {
            this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
          }
        }, (err) => {
          this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
        })
      } else {
        // tslint:disable-next-line:max-line-length
        console.log(this.desde, 'pli');
        this.ivtApi.leerProducto(info.token, {desde: this.desde, id: this.ActiveRouter.snapshot.paramMap.get('id') }).subscribe((data: any) => {
          console.log(data, 'mi data 2', this.desde);
          if ( data.exe ){
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.productos.push(hoja);
              })
              event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
      
      
    }
    }

    cambioFiltro( event ) {
      this.seleccion = event.detail.value;

      if (this.seleccion === 'Todos') {
        this.prod = true;
        this.cargarProductos();
      } else if (this.seleccion === 'Agotados') {
        this.prod = false;
        this.cargarProductosAgotados();
      } else if (this.seleccion === 'Próximos en agotarse') {
        this.prod = false;
        this.cargarProductosProximos();
      }
    }


    async cargarProductosAgotados() {
      
      const info = await this.almacenamiento.obtenerToken();
  
      if ( info ) {
        this.ivtApi.leerAgotados(info.token, {_id: this.ActiveRouter.snapshot.paramMap.get('id') } ).subscribe((data: any) => {
          console.log(data);
          if (data.exe) {
            if (data.response.length === 0) {
              this.mensaje = 'No hay productos agotados';
            }
            this.productos = data.response;
          } else {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
          }
          this.load = true;
        }, (err) => {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        })
      }
  }


  async cargarProductosProximos() {
      
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.ivtApi.leerProximos(info.token, {_id: this.ActiveRouter.snapshot.paramMap.get('id') } ).subscribe((data: any) => {
        if (data.exe) {
          if (data.response.length === 0) {
            this.mensaje = 'No hay productos próximos para agotarse';
          }
          this.productos = data.response;
        } else {
          this.toast.toastError('No se pudo cargar los datos v2, intentelo más tarde');
        }
        this.load = true;
      }, (err) => {
          this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
      })
    }
}
  
    

}
